<template>
  <v-dialog fullscreen persistent
    v-model="paymentOpen"
    content-class="background-cover"
  >
    <order-processing v-if="state === states.ORDER_PROCESSING"/>
    <awaiting-payment v-if="state === states.AWAITING_PAYMENT"
      @pay="pay"
    />
    <payment-processing v-if="state === states.PAYMENT_PROCESSING"/>
    <payment-done v-if="state === states.PAYMENT_DONE"/>
  </v-dialog>
</template>

<script>
import { mapState } from 'vuex';
import OrderProcessing from './OrderProcessing.vue';
import AwaitingPayment from './AwaitingPayment.vue';
import PaymentProcessing from './PaymentProcessing.vue';
import PaymentDone from './PaymentDone.vue';

let states = {
  ORDER_PROCESSING: 0,
  AWAITING_PAYMENT: 1,
  PAYMENT_PROCESSING: 2,
  PAYMENT_DONE: 3,
};

export default {
  components: {
    'order-processing': OrderProcessing,
    'awaiting-payment': AwaitingPayment,
    'payment-processing': PaymentProcessing,
    'payment-done': PaymentDone,
  },

  data() {
    return {
      states,
      state: states.ORDER_PROCESSING,
    };
  },

  computed: mapState(['paymentOpen']),

  watch: {
    paymentOpen(val) {
      if (val) this.init();
    },
  },

  methods: {
    init() {
      this.state = this.states.ORDER_PROCESSING;
      setTimeout(() => { this.state = this.states.AWAITING_PAYMENT }, 2000);
    },
    pay() {
      this.state = this.states.PAYMENT_PROCESSING;
      setTimeout(() => { this.state = this.states.PAYMENT_DONE }, 1500);
      setTimeout(() => { this.$store.commit('closePayment') }, 3000);
    },
  },
};
</script>

<style lang="scss">
@import '@/assets/css/main.scss';

.background-cover {
  background: rgba(black, 0.85);
  color: white;
}

.progress-circular {
  color: $primary-color;
}
</style>
