import Vue from 'vue';
import Vuex from 'vuex';
import orders from './modules/orders';
import cart from './modules/cart';
import api from './modules/api';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    orders,
    cart,
    api,
  },

  state: {
    menuOpen: false,
    cartOpen: false,
    paymentOpen: false,
    toolbarOpen: false,
    activeStallUid: null,

    customer: {
      id: 104,
      type: 'school',
    },
  },

  getters: {
    activeMenu(state) {
      let uid = state.activeStallUid;
      return state.api.menu[uid];
    },
  },

  mutations: {
    toggleMenu(state) {
      state.menuOpen = !state.menuOpen;
    },

    openPayment(state) {
      state.paymentOpen = true;
    },
    closePayment(state) {
      state.paymentOpen = false;
    },

    setActiveStall(state, uid) {
      state.activeStallUid = uid;
      state.cart.activeStallUid = uid;
    },
  },

  actions: {

  },
});
