import Vue from 'vue';
import Router from 'vue-router';
import Login from './views/Login.vue';
import Main from './views/Main.vue';
import Nearby from './views/Nearby.vue';
import Orders from './views/Orders.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login,
    },
    {
      path: '/main',
      name: 'Main',
      component: Main,
    },
    {
      path: '/nearby',
      name: 'Nearby',
      component: Nearby,
    },
    {
      path: '/orders',
      name: 'Orders',
      component: Orders,
    },
  ],
});
